/* ------------------------------
 * Containers
------------------------------ */
.container {max-width: 1348px; padding-left: 1.5rem; padding-right: 1.5rem}

/* ------------------------------
 * Loading spinner
------------------------------ */
.spinner-wrapper {display: flex; padding: 120px 0; align-items: center; justify-content: center}
.spinner-wrapper .spinner {position: relative; display: inline-block; width: 5rem; height: 5rem; color: #3e77fe}
.spinner-wrapper .spinner div {position: absolute; width: 4rem; height: 4rem; margin: 0.5rem; border: 0.5rem solid currentColor; border-radius: 50%; border-color: currentColor transparent transparent transparent; animation: spinner 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite}
.spinner-wrapper .spinner div:nth-child(1) {animation-delay: -0.45s}
.spinner-wrapper .spinner div:nth-child(2) {animation-delay: -0.3s}
.spinner-wrapper .spinner div:nth-child(3) {animation-delay: -0.15s}
@keyframes spinner {
    0% {transform: rotate(0deg)}
    100% {transform: rotate(360deg)}
}

/* ------------------------------
 * Fonts
------------------------------ */
.text-danger {color: #f03e3e !important}
.font-weight-medium {font-weight: 500 !important}

/* ------------------------------
 * Symbols
------------------------------ */
.px-symbol-deco {position: relative; display: block; width: 24px; height: 24px; border: 2px solid #3e77fe; overflow: hidden}
.px-symbol-deco:before {content: ''; position: absolute; top: 2px; right: -3px; width: 12px; height: 2px; border-bottom: 2px solid #3e77fe; transform: rotate(-45deg)}
.px-symbol-deco.large {width: 32px; height: 32px}
.px-symbol-deco.large:before {top: 3px; width: 16px}

