.button:disabled, .button.disabled {pointer-events: none; opacity: .5}
.link-inline {font-weight: 500; color: #3e77fe !important}


/* ------------------------------
 * Forms
------------------------------ */
.form-item {display: block; width: 100%; height: 50px; font-size: 14px; color: #555; line-height: 1.42; padding: 8px 18px; border: 1px solid #ced4da; border-radius: 3px; background-color: #fff; outline: none; transition: all .25s}
.form-item:focus {border-color: #3e77fe; background-color: #f4f7ff; box-shadow: 0 6px 10px 0 rgba(59,91,219,.1)}
textarea.form-item {height: auto; padding: 20px 18px}
.form-item[disabled], .form-item.disabled, .form-item[readonly], .form-item.readonly {border-color: #dee2e6}
.form-item[disabled], .form-item.disabled {background-color: #f8f9fa}
.form-item[readonly]:focus {border-color: #dee2e6; background-color: #f8f9fa; box-shadow: none}
.form-item.error {border-color: #fa5252; background-color: #fff5f5}
.px-check-form {position: relative}
.px-check-form input[type='checkbox'], .px-check-form input[type='radio'] {position: absolute; top: -1px; left: -1px; opacity: 0; visibility: hidden}
.px-check-form input[type='checkbox'] + label, .px-check-form input[type='radio'] + label {position: relative; display: inline-block; font-size: 15px; font-weight: 500; color: #666; vertical-align: middle; cursor: pointer}
.px-check-form input[type='checkbox'] + label:before, .px-check-form input[type='radio'] + label:before {content: ''; display: inline-block; width: 24px; height: 24px; margin-right: 8px; border: 2px solid #dee2e6; border-radius: 3px; background-color: #fff; vertical-align: middle}
.px-check-form input[type='checkbox'] + label:after, .px-check-form input[type='radio'] + label:after {content: '\e928'; position: absolute; top: 1px; left: 5px; font-family: 'xeicon'; color: transparent}
.px-check-form input[type='checkbox']:checked + label:before, .px-check-form input[type='radio']:checked + label:before {border-color: #3e77fe; background-color: #3e77fe}
.px-check-form input[type='checkbox']:checked + label:after, .px-check-form input[type='radio']:checked + label:after {color: #fff}
.px-check-form label a {display: inline-block; color: #3e77fe; border-bottom: 1px solid #3e77fe; vertical-align: middle}
.px-custom-file {position: relative; display: block; width: 100%}
.px-custom-file input[type='file'] {position: absolute; top: -1px; left: -1px; width: 1px; height: 1px; opacity: 0; visibility: hidden}
.px-custom-file input[type='text'] {width: calc(100% - 11.1875rem) !important}
.px-custom-file input[type='file'] ~ label {position: absolute; top: 0; right: 0; display: inline-block; font-size: 1rem; font-weight: 700; color: #fff; letter-spacing: -0.04em; line-height: 3.625rem; text-align: center; width: 11.25rem; height: 3.75rem; margin-bottom: 0; background-color: #212529; cursor: pointer; transition: color .25s, background-color .25s, box-shadow .25s}
.px-custom-file input[type='file']:not(:disabled) ~ label:hover {background-color: #3e77fe; box-shadow: 0 0 12px 0 rgba(59,91,219,.4)}
.px-custom-file input[type='file']:disabled ~ label {opacity: .5; pointer-events: none}
.px-custom-file input[type='file'] ~ label svg {margin-right: 0.5rem}

.px-custom-check label {position: relative; font-size: 0; margin-bottom: 0; cursor: pointer; vertical-align: top}
.px-custom-check input[type='checkbox'] {position: absolute; top: -1px; left: -1px; width: 0; height: 0; opacity: 0; pointer-events: none; visibility: hidden}
.px-custom-check .check-helper {position: relative; top: 0.125rem; display: inline-block; width: 1.25rem; height: 1.25rem; margin-right: 0.625rem; border: 2px solid #ccc; background-color: transparent; vertical-align: middle}
.px-custom-check .check-helper::before {content: ''; position: absolute; display: block; top: 50%; left: 50%; width: 1.125rem; height: 1.125rem; background-color: #212529; transform: translate(-50%,-50%) scale(0,0); transform-origin: 50% 50%; transition: transform .1s}
.px-custom-check label span {display: inline-block; font-size: 1rem; font-weight: 500; color: #868e96; letter-spacing: -0.04em; vertical-align: middle}
.px-custom-check label span em {font-style: normal}
.px-custom-check label:hover .check-helper, .px-custom-check input[type='checkbox']:checked + .check-helper {border-color: #212529}
.px-custom-check input[type='checkbox']:checked + .check-helper::before {transform: translate(-50%,-50%) scale(1,1)}
.px-custom-check input[type='checkbox']:checked ~ span {color: #212529}
.px-custom-check input[type='checkbox']:disabled + .check-helper {border-color: #ccc !important; opacity: .5}
.px-custom-check input[type='checkbox']:disabled ~ span {opacity: .5}
.px-custom-check.check-large .check-helper {top: 0.0625rem; width: 1.625rem; height: 1.625rem}
.px-custom-check.check-large .check-helper::before {width: 1.5rem; height: 1.5rem}
.px-custom-check.check-large label span {font-size: 1.125rem}

.selectric {border-color: #ccc; background-color: #fff}
.selectric-wrapper:not(.selectric-disabled) .selectric:hover, .selectric-open .selectric {border-color: #212529}
.selectric .label {font-size: 16px; font-weight: 500; color: #868e96; letter-spacing: -0.04em; text-align: left; line-height: 60px; height: 60px; margin: 0; padding: 0 24px}
.selectric .button {width: 50px; height: 60px; padding: 0; color: #666; line-height: 48px; background-color: #fff}
.selectric .button:after {top: 50%; left: 50%; bottom: auto; right: auto; width: 17px; height: 10px; border: 0; background-image: url(../images/common/selectric_dropdown_arrow.svg); background-size: 100%; background-position: center; background-repeat: no-repeat; transform: translate(-50%,-50%)}
.selectric-items {margin-top: -1px; border-color: #212529; background-color: #fff; box-shadow: none}
.selectric-above .selectric-items {margin-bottom: -1px}
.selectric-items ul, .selectric-items li {font-size: 16px; color: #495057; letter-spacing: -0.04em}
.selectric-items li {padding: 14px 24px}
.selectric-items li:hover {background-color: #f1f3f5}
.selectric-items li.highlighted {background-color: #f1f3f5}
.selectric-items li.selected {color: #fff; background-color: #212529}
label.error {font-size: 14px; color: #f03e3e; margin-top: 8px; margin-bottom: 0}

/* ------------------------------
 * Tooltips
------------------------------ */
.tooltip {font-family: inherit; font-weight: 700}
.tooltip-inner {font-size: 13px; color: #333; padding: 16px; border-radius: 8px; background-color: #f1f3f5}
.tooltip.top {padding: 5px 0 12px}
.bs-tooltip-auto[x-placement^=top] .arrow::before,.bs-tooltip-top .arrow::before {border-top-color: #f1f3f5}
.tooltip.bottom {padding: 12px 0 5px}
.bs-tooltip-auto[x-placement^=bottom] .arrow::before,.bs-tooltip-bottom .arrow::before {border-bottom-color: #f1f3f5}
.tooltip.left {padding: 0 12px 0 5px}
.bs-tooltip-auto[x-placement^=left] .arrow::before,.bs-tooltip-left .arrow::before {border-left-color: #f1f3f5}
.tooltip.right {padding: 0 5px 0 12px}
.bs-tooltip-auto[x-placement^=right] .arrow::before,.bs-tooltip-right .arrow::before {border-right-color: #f1f3f5}

/* ------------------------------
 * Utilities
------------------------------ */
.pointer-disable {-ms-user-select: none; -moz-user-select: none; -webkit-user-select: none; user-select: none; pointer-events: none}

/* ------------------------------
 * Animations
------------------------------ */
.animate-line {display: block; overflow: hidden}
.animate-line span {display: block; transform: translate(0,100%); transition: transform .9s cubic-bezier(.17,1.02,.53,.98)}
.animate-line.animated span {transform: translate(0,0)}
[data-aos=px-fade] {opacity: 0; transform: translate3d(0, 50px, 1px) scaleY(1.3); transform-origin: 0 0; transition: opacity 1s ease, transform 1s ease; transition-duration: 1s !important}
[data-aos=px-fade].aos-animate {opacity: 1; transform: none}
[data-aos=fade-up-short] {transform:translate3d(0, 30px ,0)}
[data-aos=fade-down-short] {transform:translate3d(0, -30px ,0)}
[data-aos=fade-left-short] {transform:translate3d(30px, 0 ,0)}
[data-aos=fade-right-short] {transform:translate3d(-30px, 0 ,0)}
.lazyload {position: relative; overflow: hidden}
.lazyload > [class*='lazyload'] {position: absolute; top: 0; left: 0; bottom: 0; right: 0; transition: opacity .1s}
.lazyload > .lazyload-overlay {background-color: #e9ecef; z-index: 1}
.lazyload > .lazyload-mask {background-color: #4c6ef5; transform: translate(-100%); z-index: 2}
.lazyload.is-animated > .lazyload-overlay {opacity: 0; transition-delay: .8s}
.lazyload.is-animated > .lazyload-mask {animation: lazyload 1s cubic-bezier(.19,1,.22,1) .4s}
.px-expand-section {clip: rect(0, 50vw, 100vh, 50vw); transition: clip 1s cubic-bezier(.19,1,.22,1)}
.px-expand-section.is-animated {clip: rect(0, 100vw, 100vh, 0)}
.no-delay {transition-duration: 0ms !important}

@keyframes lazyload {
    0% {transform: translate(-100%)}
    50% {transform: translate(0%)}
    100% {transform: translate(100%)}
}

@keyframes fadeInUpShort {
    from {opacity: 0; transform: translate3d(0, 30px, 0)}
    to {opacity: 1; transform: translate3d(0, 0, 0)}
}

.fadeInUpShort {animation-name: fadeInUpShort}

@keyframes fadeInDownShort {
    from {opacity: 0; transform: translate3d(0, -30px, 0)}
    to {opacity: 1; transform: translate3d(0, 0, 0)}
}

.fadeInDownShort {animation-name: fadeInDownShort}

@keyframes fadeOutUpShort {
    from {opacity: 1; transform: translate3d(0, 0, 0)}
    to {opacity: 0; transform: translate3d(0, -30px, 0)}
}

.fadeOutUpShort {animation-name: fadeOutUpShort}

@keyframes fadeOutDownShort {
    from {opacity: 1; transform: translate3d(0, 0, 0)}
    to {opacity: 0; transform: translate3d(0, 30px, 0)}
}

.fadeOutDownShort {animation-name: fadeOutDownShort}

/* ------------------------------
 * Modals
------------------------------ */
.modal-backdrop {background-color: #999}
.modal-backdrop.show {opacity: .4}
.modal-content {border: 0; border-radius: 0}
.modal-header {padding: 2.5rem 3rem 1.75rem; border-bottom: 0; border-top-left-radius: 0; border-top-right-radius: 0}
.modal-title {font-size: 1.75rem; font-weight: 700; color: #212529; letter-spacing: -0.06em}
.modal-header .close {width: 3.75rem; height: 3.75rem; font-size: 24px; color: #fff; margin: -0.625rem 0 auto; padding: 0; border-radius: 50%; background-color: #000; opacity: 1 !important; outline: 0}
.modal-body {padding: 1.75rem 3rem}
.modal-body p {line-height: 1.6}
.modal-footer .footer-button {display: block; width: 100%; font-size: 1rem; font-weight: 700; color: #333; line-height: 1.44; text-align: center; margin: 0 !important; padding: 1.5rem 0.625rem; border: 0; background-color: transparent}
.modal-footer .footer-button:active {background-color: #f8f9fa}
.modal-sm .modal-header {padding: 1.5rem 1.75rem}
.modal-sm .modal-title {font-size: 1.375rem}
.modal-sm .modal-header .close {width: 2.5rem; height: 2.5rem; font-size: 1.125rem; margin: -0.25rem 0 auto}
.modal-body .notify-container h5 {font-size: 1.375rem; font-weight: 700; color: #212529; letter-spacing: -0.04em; margin-bottom: 16px}
.modal-body .notify-container p {font-size: 1.0625rem; letter-spacing: -0.04em; line-height: 1.8}
.modal-body .notify-container ul li {position: relative; color: #495057; letter-spacing: -0.04em; line-height: 1.67; padding-left: 0.9375rem}
.modal-body .notify-container ul li::before {content: ''; position: absolute; top: 0.6875rem; left: 0; width: 0.25rem; height: 0.25rem; border-radius: 50%; background-color: #ccc}
.modal-body .notify-container ul li + li {margin-top: 0.375rem}
.modal-body .notify-container ol {counter-reset: notifyCounter}
.modal-body .notify-container ol li {position: relative; color: #495057; letter-spacing: -0.04em; line-height: 1.67; padding-left: 1.125rem; counter-increment: notifyCounter}
.modal-body .notify-container ol li::before {content: counter(notifyCounter) '.'; position: absolute; top: 0; left: 0}
.modal-body .notify-container ol li + li {margin-top: 0.375rem}

.swal2-popup {padding: 0 !important; border-radius: 3px !important; overflow: hidden; box-shadow: 0 40px 80px -16px rgba(0,0,0,.16)}
.swal2-header {padding: 2.25rem 15px 1rem}
.swal2-title {font-size: 1.3125rem !important; font-weight: 700; color: #2b2b2b !important; margin: 0 !important}
.swal2-content {font-size: 1rem !important; line-height: 1.6; padding: 0 15px !important}
.swal2-content .modal-textarea {display: block; width: 100%; height: auto; margin: 2.25rem 0; padding: 1.75rem; font-size: 14px; color: #868e96; line-height: 1.85; border: 1px solid #ced4da; border-radius: 3px; outline: 0}
.swal2-actions {margin: 2.125rem auto 0 !important; padding: 1.375rem 15px 1.5rem; background-color: #edf2ff}
.swal2-styled.swal2-confirm {font-size: 15px !important; font-weight: normal; color: #fff; padding: 0.875rem 2.5rem; border-radius: 3px !important; background-color: #3e77fe !important; box-shadow: 0 4px 10px 0 rgba(62, 119, 254, .4) !important}
.swal2-close {font-family: sans-serif !important; font-weight: 100}

/* ------------------------------
 * Videos
------------------------------ */
.youtube-embed-wrapper {position: relative}
.youtube-embed-wrapper embed, .youtube-embed-wrapper iframe {position: absolute; display: block; width: 100% !important; height: 100% !important}

/* ------------------------------
 * Media queries
------------------------------ */
@media (min-width: 576px) {
    .modal-sm {max-width: 360px}
}

@media (max-width: 575px) {
    .modal-header {padding: 1.75rem}
    .modal-header .close {width: 3rem; height: 3rem; margin: -0.5rem 0 auto}
    .modal-title {font-size: 1.5rem}
    .modal-body {padding: 1.75rem}
    .modal-body .notify-container h5 {font-size: 1.1875rem}
}