.news-list .nav-tabs {margin-bottom: 8.125rem; border: 0}
.news-list .nav-tabs .nav-item {margin-bottom: 0}
.news-list .nav-tabs .nav-item + .nav-item {margin-left: 2.625rem}
.news-list .nav-tabs .nav-link {font-size: 1.3125rem; font-weight: 700; color: #adb5bd; letter-spacing: -0.04em; padding: 0 0 6px; border: 0; border-radius: 0; transition: color .25s}
.news-list .nav-tabs .nav-link::before {content: ''; display: block; width: 0.75rem; height: 1px; margin-bottom: 0.375rem; margin-left: 2px; transform: rotate(-45deg); background-color: transparent; transform-origin: 0 0; transition: background-color .25s}
.news-list .nav-tabs .nav-item.show .nav-link, .news-list .nav-tabs .nav-link.active {color: #3e78ff; border-radius: 0; border: 0}
.news-list .nav-tabs .nav-item.show .nav-link::before, .news-list .nav-tabs .nav-link.active::before {background-color: #3e78ff}
.news-list .col-article {margin-bottom: 5rem}
.news-list .col-article a {display: block; height: 100%}
.news-list .col-article figure {margin-bottom: 2.125rem; border-radius: 0.375rem; overflow: hidden; transition: box-shadow .3s, transform .3s}
.news-list .col-article figure img {display: block; width: 100%}
.news-list .col-article .article-info {padding-left: 1.75rem; padding-right: 1.75rem}
.news-list .col-article .article-info h4 {font-size: 1.5rem; font-weight: 700; color: #000; letter-spacing: -0.04em; margin-bottom: 1.25rem}
.news-list .col-article .article-info p {display: -webkit-box; font-size: 1.0625rem; font-weight: 300; color: #495057; letter-spacing: -0.06em; line-height: 1.76; text-overflow: ellipsis; height: 3.75rem; overflow: hidden; -webkit-box-orient: vertical; -webkit-line-clamp: 2}
.news-list .col-article .article-info .bottom-info {margin-top: 1.75rem; font-size: 0}
.news-list .col-article .article-info .bottom-info span {display: inline-block; font-size: 0.9375rem}
.news-list .col-article .article-info .bottom-info .category {font-weight: 700; color: #3e78ff; letter-spacing: -0.06em}
.news-list .col-article .article-info .bottom-info .category::after {content: ''; display: inline-block; width: 4px; height: 4px; margin: 0 12px; border-radius: 50%; background-color: #3e78ff; vertical-align: middle}
.news-list .col-article .article-info .bottom-info .date {color: #adb5bd}
.news-list .col-article a:hover figure {box-shadow: 0 1.25rem 1.25rem 0 rgba(12,12,12,.12); transform: scale(1.02)}
.news-list .no-content {text-align: center; padding-bottom: 5rem}
.news-list .no-content p {position: relative; display: inline-block; font-size: 1.0625rem; font-weight: 500; color: #868e96; padding-left: 1.75rem}
.news-list .no-content p::before {content: ''; position: absolute; top: 3px; left: 0; display: block; width: 1.25rem; height: 1.25rem; background-image: url(../../static/images/sub/no_content_bullet.svg); background-size: 100%; background-position: center; background-repeat: no-repeat}
.news-list .show-more {padding-top: 3.75rem}

.news-view .article-header {padding-bottom: 1.5rem; border-bottom: 3px solid #212529}
.news-view .article-header .num {display: inline-block; font-size: 2.625rem; font-weight: 700; color: #000; margin-bottom: 1rem}
.news-view .article-header .col-left h2 {font-size: 2.75rem; font-weight: 700; color: #212529; letter-spacing: -0.05em; line-height: 1.28; margin-bottom: 1.5rem}
.news-view .article-header .col-left ul {font-size: 0}
.news-view .article-header .col-left ul > li {display: inline-block; font-size: 0.9375rem; font-weight: 500; color: #999; vertical-align: middle}
.news-view .article-header .col-left ul > li + li::before {content: ''; display: inline-block; width: 3px; height: 3px; margin: 0 10px; border-radius: 50%; background-color: #ccc; vertical-align: 4px}
.news-view .article-header .col-right {padding-top: 0.75rem}
.news-view .article-header .col-right .sns-share-items {font-size: 0}
.news-view .article-header .col-right .sns-share-items li {display: inline-block}
.news-view .article-header .col-right .sns-share-items li + li {margin-left: 1rem}
.news-view .article-header .col-right .sns-share-items li a {display: block; width: 3rem; height: 3rem; font-size: 1.3125rem; color: #333; text-align: center; line-height: 3rem; border-radius: 50%; background-color: #e9ecef; transition: color .2s, background-color .2s}
.news-view .article-header .col-right .sns-share-items li a.sns-kakao:hover {background-color: #f7e600}
.news-view .article-header .col-right .sns-share-items li a.sns-facebook:hover {color: #fff; background-color: #3b5998}
.news-view .article-header .col-right .sns-share-items li a.sns-twitter:hover {color: #fff; background-color: #55acee}
.news-view .article-header .col-right .sns-share-items li a.sns-google:hover {color: #fff; background-color: #dd4b39}
.news-view .article-header .col-right .sns-share-items li a.sns-link:hover {background-color: #dee2e6}
.news-view .article-header .col-right .sns-share-items li a i {line-height: 1}
.news-view .article-body {padding-top: 5rem; padding-bottom: 5rem; font-size: 1.125rem !important; color: #343a40; line-height: 1.8; letter-spacing: -0.05em}
.news-view .article-body img {height: auto !important}
.news-view .article-body p {margin-bottom: 1rem}
.news-view .article-body font, .news-view .article-body span {font-family: inherit !important; font-size: inherit !important}
.news-view .article-body .additional-link-items {margin-top: 3rem}
.news-view .article-body .additional-link-items li {position: relative; color: #666; padding-left: 0.75rem}
.news-view .article-body .additional-link-items li::before {content: ''; position: absolute; top: 0.875rem; left: 0; width: 0.25rem; height: 4px; border-radius: 50%; background-color: #3e77fe}
.news-view .article-body .additional-link-items li a {color: #3e77fe; word-break: break-all; margin-left: 0.625rem}
.news-view .article-body .additional-link-items li a:hover {text-decoration: underline !important}
.news-view .article-tail {padding-top: 5rem; border-top: 1px solid #d9d9d9}
.news-view .article-tail .btn-list {display: inline-block; font-size: 1.375rem; font-weight: 500; color: #fff; letter-spacing: -0.04em; padding: 1.5rem 3.75rem; background-color: #212529}
.news-view .article-tail .col-nav > a {display: block; font-size: 0}
.news-view .article-tail .col-nav > a > .icon, .news-view .article-tail .col-nav > a > .post-info {display: inline-block; vertical-align: top}
.news-view .article-tail .col-nav > a > .icon {width: 3rem; font-size: 3rem; color: #ccc; transition: color .2s}
.news-view .article-tail .col-nav > a > .icon svg {width: 2.25rem; height: 2.25rem}
.news-view .article-tail .col-nav > a > .post-info {width: calc(100% - 3rem)}
.news-view .article-tail .col-nav > a > .post-info span {display: block; font-size: 2.5rem; font-weight: 600; color: #ccc; transition: color .2s}
.news-view .article-tail .col-nav > a > .post-info p {font-size: 1.0625rem; font-weight: 500; color: #212529; letter-spacing: -0.04em}
.news-view .article-tail .col-nav > a:hover > .icon {color: #000}
.news-view .article-tail .col-nav > a:hover > .post-info span {color: #000}

@media (max-width: 767px) {
    .news-list .nav-tabs {margin-bottom: 5rem}
    .news-list .news-row {margin-left: -4px !important; margin-right: -4px !important}
    .news-list .col-article {margin-bottom: 3rem; padding-left: 4px !important; padding-right: 4px !important}
    .news-list .col-article figure {margin-bottom: 1.5rem}
    .news-list .col-article .article-info {padding-left: 1.25rem; padding-right: 1.25rem}
    .news-list .col-article .article-info h4 {font-size: 1.25rem}
    .news-list .col-article .article-info p {font-weight: normal}
    .news-view .article-header .num {font-size: 1.5rem; margin-bottom: 0.5rem}
    .news-view .article-header .col-left h2 {font-size: 1.75rem; margin-bottom: 1rem}
    .news-view .article-header .col-right .sns-share-items li + li {margin-left: 0.5rem}
    .news-view .article-body {padding-top: 4rem; padding-bottom: 4rem}
    .news-view .article-tail {padding-top: 4rem}
    .news-view .article-tail .btn-list {font-size: 1.125rem; padding: 1.375rem 3rem}
    .news-view .article-tail .col-nav > a > .icon, .news-view .article-tail .col-nav > a > .post-info {vertical-align: middle}
    .news-view .article-tail .col-nav > a > .icon {font-size: inherit; width: 2rem}
    .news-view .article-tail .col-nav > a > .icon svg {width: 1.75rem; height: 1.75rem}
    .news-view .article-tail .col-nav > a > .post-info {width: calc(100% - 2rem)}
    .news-view .article-tail .col-nav > a > .post-info span {display: inline-block; font-size: 2rem; vertical-align: middle}
}

@media (max-width: 575px) {
    .news-list .col-article {margin-bottom: 0}
    .news-list .col-article + .col-article {margin-top: 3rem}
    .news-view .article-tail .btn-list {display: block; width: 100%; padding: 1.375rem 0.625rem}
}