.notfound {text-align: center}
.notfound .notfound-header {font-size: 10rem; font-weight: 900; color: #3f78ff}
.notfound .sub-header {font-size: 2.375rem; font-weight: 700; color: #212529; letter-spacing: -0.04em; margin-top: 2rem; margin-bottom: 1rem}
.notfound p {font-size: 1.125rem; color: #212529; letter-spacing: -0.04em}
.notfound .button-wrapper {margin-top: 5rem; font-size: 0}
.notfound .button-wrapper > a {display: inline-block; font-size: 0.9375rem; font-weight: 700; color: #333; letter-spacing: -0.04em; padding: 0.75rem 1.5rem; border: 2px solid #333; border-radius: 2.5rem}
.notfound .button-wrapper > a + a {margin-left: 0.5rem}

@media (max-width: 767px) {
    .notfound .notfound-header {font-size: 7.5rem}
    .notfound .sub-header {font-size: 1.5rem}
}