/* ------------------------------
 * Hero
------------------------------ */
.hero {position: relative; height: 100vh; background-color: #333}
.hero:after {content: ''; position: absolute; top: 0; left: 0; bottom: 0; right: 0; background-color: #000; opacity: .25; z-index: 1}
.hero .hero-video {position:absolute; top:0; left:0; width:100%; height:100%; object-fit:cover}
.hero .caption-wrap {position: absolute; display: block; top: 0; left: 0; bottom: 0; right: 0; max-width: 1530px; margin-left: auto; margin-right: auto; padding-left: 1.5rem; padding-right: 1.5rem; z-index: 10}
.hero .caption-wrap h2 {font-size: 3.5rem; font-weight: 700; color: #fff; line-height: 1.38; letter-spacing: -0.05rem}
.hero .caption-wrap p {font-size: 0.8125rem; font-weight: 700; color: #fff; letter-spacing: 0.0625rem; margin-bottom: 1.5rem}
.hero .caption-wrap .button-more {display: inline-block; font-size: 0.875rem; font-weight: 600; color: #fff; padding: 1rem 1.875rem; border: 1px solid #fff; border-radius: 2.25rem; transition: color .25s, border-color .25s, background-color .25s}
.hero .caption-wrap .button-more:hover {border-color: #3e78ff; background-color: #3e78ff}
.hero .caption-inner {padding-bottom: 3.75rem}
.hero .scroll-indicator {position: absolute; bottom: 5rem; left: 0; right: 0; width: 2.25rem; height: 3.625rem; margin: auto; border: 0.1875rem solid #fff; border-radius: 2.25rem; z-index: 10; cursor: pointer}
.hero .scroll-indicator .wheel {position: absolute; top: 0.5rem; left: 50%; width: 0.1875rem; height: 0.5rem; margin-left: -0.0938rem; border-radius: 0.375rem; background-color: #fff; animation: wheel-ani 1s linear infinite}
@keyframes wheel-ani {
    0% {opacity: 0; transform: translateY(0)}
    25% {opacity: 1; transform: translateY(0.625rem)}
    75% {opacity: 1; transform: translateY(0.625rem)}
    100% {opacity: 0; transform: translateY(0.625rem)}
}

/* ------------------------------
 * Common
------------------------------ */
.section {position: relative; padding-top: 12.5rem; padding-bottom: 12.5rem}
.section .section-heading {position: relative; margin-bottom: 6.875rem; z-index: 10}
.section .section-heading h2 {font-size: 4.25rem; font-weight: 700; color: #222}
.section .section-heading h2::before {content: ''; display: block; width: 3.9375rem; height: 0.6875rem; background-image: url(../../static/images/main/wave_heading_bullet.svg); background-size: 100%; background-position: center; background-repeat: no-repeat}
.section .section-heading h2 span {display: inline-block}
.section .section-heading h2 strong {color:#3e78ff}
.section .section-heading p {font-size: 1.1875rem; color: #333; line-height: 1.8; letter-spacing: -0.05rem}

/* ------------------------------
 * About
------------------------------ */
.about .col-text p {font-size: 1.1875rem; color: #343a40; line-height: 1.89}

/* ------------------------------
 * Project
------------------------------ */
.project .col-project {margin-bottom: 1.875rem}
.project .col-project .col-inner {border-radius: 0.375rem; overflow: hidden; transition: box-shadow .3s, transform .3s}
.project .col-project .col-inner:hover {box-shadow: 0 1.25rem 1.25rem 0 rgba(12,12,12,.12); transform: scale(1.02)}
.project .col-project .col-inner a {position: relative; display: block}
.project .col-project .col-inner a img {display: block; width: 100%; height: auto}
.project .col-project .col-inner .project-info {position: absolute; top: 0; left: 0; right: 0; padding: 2.625rem; z-index: 5}
.project .col-project .col-inner .project-info .category {font-size: 0.9375rem; font-weight: 700; color: #3e77fe; margin-bottom: 1rem}
.project .col-project .col-inner .project-info h3 {font-size: 1.4375rem; font-weight: 700; color: #222; line-height: 1.4; letter-spacing: -0.04em}
.project .show-all {padding-top: 5rem}

/* ------------------------------
 * What we do
------------------------------ */
.what-we-do {padding-top: 5rem; padding-bottom: 10rem}
.what-we-do .cover {height: 35rem; margin-top: -9.0625rem; /*margin-left: 5rem; margin-right: 5rem;*/ background-color: #f7f7f7}
.what-we-do .cover .col-list {padding-top: 5.625rem}
.what-we-do .cover .col-list ul li {position: relative; padding-left: 3rem}
.what-we-do .cover .col-list ul li .px-symbol-deco {position: absolute; top: 0.375rem; left: 0}
.what-we-do .cover .col-list ul li .numbering {position: absolute; top: 0.25rem; left: 0; font-size: 0.875rem; font-weight: 700; color: #3e78ff}
.what-we-do .cover .col-list ul li h3 {font-size: 1.3125rem; font-weight: 700; color: #212529; letter-spacing: -0.05rem; margin-bottom: 0.5rem}
.what-we-do .cover .col-list ul li p {font-size: 1.0625rem; color: #343a40; line-height: 1.85; letter-spacing: -0.05rem}
.what-we-do .cover .col-list ul li + li {margin-top: 3rem}
.what-we-do .cover .col-img {position: relative}
.what-we-do .cover .col-img .lazyload {position: absolute; top: -2.5rem; box-shadow: 2.5rem 2.5rem 5rem -0.625rem rgba(0,0,0,.17)}
.what-we-do .cover .col-img .img-01 {z-index: 5}
.what-we-do .cover .col-img .img-02 {z-index: 10; transform: translate(6.25rem, 6.25rem)}

/* ------------------------------
 * News
------------------------------ */
.news {background-color: #edf2ff}
.news .col-article .category {display: block; font-size: 1.4375rem; font-weight: 600; color: #3e77fe}
.news .col-article .category::after {content: ''; display: block; width: 2.5rem; height: 0.4375rem; margin: 1.125rem 0; background-image: url(../../static/images/main/wave_heading_bullet.svg); background-size: 100%; background-position: center; background-repeat: no-repeat}
.news .col-article h3 {display: -webkit-box; font-size: 1.5rem; font-weight: 700; color: #222; letter-spacing: -0.04em; line-height: 1.33; height: 4rem; margin-bottom: 2.25rem; text-overflow: ellipsis; overflow: hidden; -webkit-box-orient: vertical; -webkit-line-clamp: 2}
.news .col-article h3 a {color: #222}
.news .col-article p {display: -webkit-box; font-size: 1.0625rem; color: #333; line-height: 1.76; max-height: 7.5rem; text-overflow: ellipsis; overflow: hidden; -webkit-box-orient: vertical; -webkit-line-clamp: 4}
.news .col-article p a {color: #333}
.news .col-article .date {display: block; font-size: 1rem; font-weight: 500; color: #868e96; letter-spacing: 0.04em; margin-top: 1.75rem}
.news .col-article .button-more {display: inline-block; font-size: 0.875rem; font-weight: 600; color: #343a40; letter-spacing: -0.02em; margin-top: 2.625rem; padding: 1.125rem 1.75rem; border: 1px solid #343a40; border-radius: 1.875rem; background-color: transparent}
.news .col-article .button-more:hover {color: #fff; background-color: #343a40}
.news .col-article .button-more i {margin-left: 0.375rem; vertical-align: -1px}

/* ------------------------------
 * Media queries
------------------------------ */
@media (max-width: 991px) {
    .hero .caption-wrap h2 {font-size: 3rem}

    .section {padding-top: 10rem; padding-bottom: 10rem}
    .section .section-heading h2 {font-size: 3.5rem}

    .what-we-do {padding-bottom: 0 !important}
    .what-we-do .cover {position: relative; height: auto; margin-top: 0; padding-top: 70%; padding-bottom: 6.25rem}
    .what-we-do .cover .col-img {position: static}
    .what-we-do .cover .col-img .lazyload {left: 50%; margin-bottom: 0; transform: translate(-50%, 0)}
    .what-we-do .cover .col-img .lazyload img {display: block; width: 100%}
    .what-we-do .cover .col-img .img-02 {transform: translate(calc(-50% + 3.75rem), 3.75rem)}

    .about .col-text p {font-size: 1.0625rem}

    .project {padding-top: 10rem}
    
    .news .col-article .category {font-size: 1.1875rem}
    .news .col-article .category::after {margin: 0.75rem 0 1.125rem}
}

@media (max-width: 767px) {
    .section {padding-top: 6.25rem; padding-bottom: 6.25rem}
    .section .section-heading {margin-bottom: 5rem}
    .section .section-heading h2 {font-size: 2.5rem}
    .section .section-heading h2::before {margin-bottom: 0.5rem}

    .about .col-text {margin-top: 2rem}

    .what-we-do .cover .col-list ul li + li {margin-top: 2.25rem}

    .project .project-row {margin-left: -4px; margin-right: -4px}
    .project .project-row .col-project {margin-bottom: 8px; padding-left: 4px; padding-right: 4px}
    .project .col-project .col-inner .project-info {padding: 1.75rem}
    .project .col-project .col-inner .project-info h3 {font-size: 1.1875rem}
    .project .show-all {padding-top: 4rem}

    .news .col-article + .col-article {margin-top: 4.5rem}
    .news .col-article h3 {display: block; height: auto; margin-bottom: 1.5rem; text-overflow: unset; -webkit-box-orient: unset}
    .news .col-article .date {margin-top: 1.25rem}
}

@media (max-width: 575px) {
    .hero .caption-wrap h2 {font-size: 2.5rem}
}