html {height: 100%}
body {position: relative; height: 100%; overflow-x: hidden; overflow-y: scroll}
#header {position: fixed; top: 0; left: 0; right: 0; z-index: 1030}
#header .logo {position: absolute; top: 1.5rem; left: 1.5rem; width: 5rem; height: 5rem; z-index: 100}
#header .logo a {position: relative; display: block; height: 100%}
#header .logo a img {position: absolute; top: 0; left: 0; display: block; width: 100%; transition: opacity .3s}
#header .logo a img.logo-color {opacity: 0}
#header .button-hamburger {position: absolute; top: 2rem; right: 1.5rem; width: 4rem; height: 4rem; border: 0; border-radius: 50%; background-color: transparent; outline: 0; z-index: 10}
#header .button-hamburger::before {content: ''; display: block; position: absolute; top: 50%; left: 50%; width: 120%; height: 120%; border-radius: 50%; background-color: #fff; z-index: 2; opacity: 0; transform: translate(-50%,-50%); transition: opacity 0s .3s}
#header .button-hamburger .hamburger-bg {position: absolute; top: 50%; left: 50%; width: 100%; height: 100%; border-radius: 50%; background-color: #3e78ff; transform: translate3d(-50%,-50%,0); transition: width .2s, height .2s, background-color .3s}
#header .button-hamburger .line {position: absolute; left: 50%; display: block; width: 1.25rem; height: 2px; margin-left: -0.625rem; background-color: #fff; z-index: 10; transition: top .3s, background-color .3s, opacity .3s, transform .3s}
#header .button-hamburger .line:nth-child(2) {top: auto; margin-top: -0.5rem}
#header .button-hamburger .line:nth-child(3) {top: 50%; margin-top: -1px}
#header .button-hamburger .line:nth-child(4) {bottom: auto; margin-top: 0.375rem}
#header .button-hamburger:hover .hamburger-bg {width: 120%; height: 120%}
#gnb-wrapper {position: fixed; top: 0; left: 0; bottom: 0; right: 0; z-index: 1010; visibility: hidden; transition: visibility 0s .7s; -webkit-backface-visibility: hidden; backface-visibility: hidden}
body:not(.nav-open) #gnb-wrapper {visibility: hidden}
#gnb-wrapper .nav-bg {position: fixed; top: 0; left: 0; bottom: 0; right: 0; background-color: #f1f1f1; pointer-events: none; opacity: 0; transition: opacity .5s .4s}
#gnb-wrapper .nav-inner {position: relative; height: 100%; opacity: 0; transition: opacity .3s .3s}
#gnb-wrapper .nav-inner > div {width: 50%; height: 100%}
#gnb-wrapper .nav-inner .cell-left {background-position: center; background-size: cover; background-repeat: no-repeat; visibility: hidden; transition: visibility 0s .7s; -webkit-backface-visibility: hidden; backface-visibility: hidden}
#gnb-wrapper .nav-inner .cell-left::after {content: ''; position: absolute; top: 0; left: 0; width: 100%; height: 100%; background-color: #f1f1f1; transition: width 0s .7s}
#gnb-wrapper .nav-inner .cell-right .cell-inner {width: 100%; padding-left: 6.25rem; padding-right: 3.75rem}
#gnb-wrapper .gnb li {margin: 0.625rem 0; transition: opacity 0s .7s, transform .6s .7s; opacity: 0; transform: translateY(2.5rem)}
#gnb-wrapper .gnb li a {font-size: 3.875rem; font-weight: 500; color: #383a3c; letter-spacing: 0}
#gnb-wrapper .gnb li a .px-symbol-deco {position: absolute; top: 50%; left: 0; margin-top: -1rem; opacity: 0; visibility: hidden; transition: opacity .45s, visibility .45s}
#gnb-wrapper .gnb li a .px-symbol-deco:before {top: 0; right: 0; transform: translate3d(1rem,0,0) rotate(-45deg); transition: transform .3s; transition-delay: .45s; transform-origin: 0 0}
#gnb-wrapper .gnb li a span {position: relative; display: inline-block; transition: transform .4s cubic-bezier(.63,.29,.25,1)}
#gnb-wrapper .gnb li a span:before {content: attr(data-menu); position: absolute; top: 0; left: 0; color: #3e77fe; white-space: nowrap; max-width: 0; background-color: #f1f1f1; overflow: hidden; transition: max-width .4s cubic-bezier(.63,.29,.25,1); transition-delay: .2s}
#gnb-wrapper .gnb li a:hover .px-symbol-deco {opacity: 1; visibility: visible; transition-delay: .2s}
#gnb-wrapper .gnb li a:hover .px-symbol-deco:before {transform: translate3d(4px,10px,0) rotate(-45deg)}
#gnb-wrapper .gnb li a:hover span {transform: translateX(3.125rem)}
#gnb-wrapper .gnb li a:hover span:before {max-width: 100%}
#gnb-wrapper .cell-right .location-info {margin-top: 4.6875rem; opacity: 0; transform: translateY(2.5rem); transition: opacity .0s 1s, transform .0s 1s}
#gnb-wrapper .cell-right .location-info:before {content: ''; display: block; width: 7.5rem; height: 2px; margin-bottom: 1.75rem; background-color: #ced4da}
#gnb-wrapper .cell-right .location-info address {color: #343a40; line-height: 1.75}
#gnb-wrapper .cell-right .location-info ul {margin-top: 1.875rem}
#gnb-wrapper .cell-right .location-info ul li {font-weight: 500; color: #212529; line-height: 1.85}
#gnb-wrapper .cell-right .location-info ul li b {display: inline-block; font-weight: 700; color: #3e77fe; margin-right: 1rem}

#header.inverse .logo a img.logo-white {opacity: 0}
#header.inverse .logo a img.logo-color {opacity: 1}

body.nav-open {overflow-y: hidden}
body.nav-open #header .button-hamburger {background-color: #fff}
body.nav-open #header .button-hamburger::before {opacity: 1; transition: opacity .3s .3s}
body.nav-open #header .button-hamburger .hamburger-bg {background-color: #f1f1f1; transition: width .7s, height .7s, background-color .3s .1s; transition-timing-function: cubic-bezier(.8,0,.2,1)}
body.nav-open #header .button-hamburger .line {background-color: #000; transition: top .7s, background-color .7s, transform .7s}
body.nav-open #header .button-hamburger .line:nth-child(2), body.nav-open #header .button-hamburger .line:nth-child(4) {top: 50%; margin-top: -1px}
body.nav-open #header .button-hamburger .line:nth-child(2) {transform: rotate(45deg)}
body.nav-open #header .button-hamburger .line:nth-child(3) {opacity: 0}
body.nav-open #header .button-hamburger .line:nth-child(4) {transform: rotate(-45deg)}
body.nav-open #gnb-wrapper {visibility: visible; transition: visibility 0s}
body.nav-open #gnb-wrapper .nav-bg {opacity: 1; transition: opacity 0s .6s, background-color .5s}
body.nav-open #gnb-wrapper .nav-inner {opacity: 1; transition: opacity .3s}
body.nav-open #gnb-wrapper .nav-inner .cell-left {visibility: visible}
body.nav-open #gnb-wrapper .nav-inner .cell-left::after {width: 0; transition: width .3s 1.8s}
body.nav-open #gnb-wrapper .gnb li {opacity: 1; transform: translateY(0); transition: opacity .6s .7s, transform .6s .7s}
body.nav-open #gnb-wrapper .gnb li:nth-child(1) {transition-delay: .85s}
body.nav-open #gnb-wrapper .gnb li:nth-child(2) {transition-delay: 1s}
body.nav-open #gnb-wrapper .gnb li:nth-child(3) {transition-delay: 1.15s}
body.nav-open #gnb-wrapper .gnb li:nth-child(4) {transition-delay: 1.3s}
body.nav-open #gnb-wrapper .gnb li:nth-child(5) {transition-delay: 1.45s}
body.nav-open #gnb-wrapper .cell-right .location-info {opacity: 1; transform: translateY(0); transition: opacity .6s 1.5s, transform .6s 1.5s}
body:not(.main) #header .logo a img.logo-white {opacity: 0}
body:not(.main) #header .logo a img.logo-color {opacity: 1}
body:not(.main) #main {padding-top: 12.5rem}

.bottom-banner {text-align: center; padding-top: 7.5rem; padding-bottom: 7.5rem; background-size: cover; background-position: center; background-repeat: no-repeat}
.bottom-banner .eng-heading {display: block; font-size: 1.0625rem; font-weight: 600; color: #fff; margin-bottom: 26px}
.bottom-banner h2 {font-size: 2.625rem; font-weight: 700; color: #fff; letter-spacing: -0.04em}
.bottom-banner .button-wrap {padding-top: 4.5rem; font-size: 0}
.bottom-banner .button-wrap .button-link {display: inline-flex; font-size: 0.9375rem; font-weight: 700; color: #fff; letter-spacing: -0.02em; padding: 0 1.75rem; height: 3.75rem; border: 1px solid #fff; border-radius: 1.875rem; background-color: transparent; align-items: center; transition: color .3s, background-color .3s}
.bottom-banner .button-wrap .button-link:hover {color: #212529; background-color: #fff}
.bottom-banner .button-wrap .button-link i {margin-left: 0.375rem; vertical-align: -1px}

#floating-kakao-button {position: fixed; bottom: 4rem; right: 4rem; display: flex; width: 4rem; height: 4rem; font-size: 2.5rem; color: #000; align-items: center; justify-content: center; border-radius: 50%; background-color: #fee500; box-shadow: 6px 6px 18px 0 rgba(228,194,0,.4); z-index: 100}

#footer {padding-top: 3.75rem; padding-bottom: 3.75rem; background-color: #fff}
#footer .col-left .summary-links {font-size: 0; margin-bottom: 1rem}
#footer .col-left .summary-links > a {display: inline-block; font-size: 15px; font-weight: 700; color: #222; letter-spacing: -0.04em; margin-right: 1.875rem}
#footer .col-left .info-list li {font-size: 0}
#footer .col-left .info-list li > b, #footer .col-left .info-list li > p {display: inline-block; font-size: 1rem; font-weight: normal; color: #343a40; letter-spacing: -0.04em; line-height: 1.64; vertical-align: top}
#footer .col-left .info-list li > b {width: 2rem}
#footer .col-left .info-list li > p {width: calc(100% - 2rem); padding-left: 1rem}
#footer .col-left .info-list li > p a {color: #343a40}
#footer .col-left .info-list li + li {margin-top: 0.25rem}
#footer .col-right {padding-top: 1.25rem}
#footer .col-right .footer-logo, #footer .col-right .copy {-ms-user-select: none; -moz-user-select: none; -webkit-user-select: none; user-select: none}
#footer .col-right .footer-logo {width: 11.25rem; margin-bottom: 0.875rem; pointer-events: none}
#footer .col-right .copy {font-size: 0.875rem; color: #999; letter-spacing: 0}

@media (max-width: 1199px) {
    #gnb-wrapper .gnb li a {font-size: 48px}

    #gnb-wrapper .cell-right .location-info {margin-top: 3.75rem}
}

@media (max-width: 991px) {
    #header .logo {width: 3.75rem; height: 3.75rem}
    #header .button-hamburger {top: 1.75rem; width: 3.5rem; height: 3.5rem}
    #gnb-wrapper .gnb li a {font-size: 38px}

    .bottom-banner h2 {font-size: 2.25rem}

    #floating-kakao-button {bottom: 1.5rem; right: 1.5rem}
}

@media (max-width: 767px) {
    html, body {font-size: 14px}

    body.nav-open #header .logo a img {transition-delay: 600ms}
    body.nav-open #header .logo a img.logo-white {opacity: 0}
    body.nav-open #header .logo a img.logo-color {opacity: 1}
    #gnb-wrapper .nav-inner .cell-left {display: none}
    #gnb-wrapper .nav-inner .cell-right {width: 100%}
    #gnb-wrapper .nav-inner .cell-right .cell-inner {padding-top: 1.5rem; padding-left: 3rem; padding-right: 3rem}
    #gnb-wrapper .cell-right .location-info {margin-top: 3rem}

    .bottom-banner {padding-top: 6.25rem; padding-bottom: 6.25rem}
    .bottom-banner h2 {font-size: 1.75rem; line-height: 1.45}
    .bottom-banner .button-wrap {padding-top: 3.625rem}

    #floating-kakao-button {bottom: 3.75rem}

    #footer .col-right {padding-top: 1.75rem}
    #footer .col-right .footer-logo {width: 11.25rem}
}

@media (max-width: 575px) {
    .bottom-banner .container {padding-left: 5px; padding-right: 5px}
    .bottom-banner .button-wrap {font-size: 0}
    .bottom-banner .button-wrap .button-link {padding: 1rem 1.25rem}

    #footer .col-left .summary-links {margin-bottom: 2rem}
    #footer .col-left .summary-links > a {display: block; margin-right: 0}
    #footer .col-left .summary-links > a + a {margin-top: 0.5rem}
}