.project-list .col-project {margin-bottom: 1.875rem}
.project-list .col-project .col-inner {border-radius: 0.375rem; overflow: hidden; transition: box-shadow .3s, transform .3s}
.project-list .col-project .col-inner:hover {box-shadow: 0 1.25rem 1.25rem 0 rgba(12,12,12,.12); transform: scale(1.02)}
.project-list .col-project .col-inner a {position: relative; display: block}
.project-list .col-project .col-inner a img {display: block; width: 100%; height: auto}
.project-list .col-project .col-inner .project-info {position: absolute; top: 0; left: 0; right: 0; padding: 2.625rem; z-index: 5}
.project-list .col-project .col-inner .project-info .category {font-size: 0.9375rem; font-weight: 700; color: #3e77fe; letter-spacing: -0.04em; margin-bottom: 1rem}
.project-list .col-project .col-inner .project-info h3 {font-size: 1.4375rem; font-weight: 700; color: #222; letter-spacing: -0.06em; line-height: 1.4}
.project-list .show-more {padding-top: 5rem}

@media (max-width: 767px) {
    .project-list .project-row {margin-left: -4px; margin-right: -4px}
    .project-list .project-row .col-project {margin-bottom: 8px; padding-left: 4px; padding-right: 4px}
    .project-list .col-project .col-inner .project-info {padding: 1.75rem}
    .project-list .col-project .col-inner .project-info h3 {font-size: 1.1875rem}
    .project-list .show-more {padding-top: 4rem}
}

