.about-intro .about-cover {margin-bottom: 6.875rem}
.about-intro .about-cover h2 {position: relative; font-size: 3.75rem; font-weight: 700; color: #fff; letter-spacing: -0.02em; line-height: 1.33; margin-top: -6.6875rem; padding-left: 5rem; z-index: 10}
.about-intro .about-cover h2 span {display: inline-block; color: #000; padding-left: 5.625rem}
.about-intro .about-row .col-heading h3 {font-size: 2.25rem; font-weight: 700; color: #212529; letter-spacing: -0.04em; line-height: 1.38}
.about-intro .about-row .col-desc p {font-size: 1.1875rem; color: #212529; line-height: 1.79}
.core-value {background-image: url(../../static/images/sub/core_value_bg.png)}
.core-value .medium-heading {margin-bottom: 8.125rem}
.core-value .col-value figure {margin-bottom: 1.375rem}
.core-value .col-value figure img {height: 3.75rem}
.core-value .col-value figure figcaption {position: relative; margin-top: -1.5rem; z-index: 5}
.core-value .col-value figure figcaption h3 {font-size: 2.25rem; font-weight: 700; color: #212529; margin-bottom: 1rem}
.core-value .col-value figure figcaption h4 {font-size: 1.5rem; font-weight: 700; color: #333; letter-spacing: -0.04em}
.core-value .col-value p {font-size: 1.1875rem; color: #495057; line-height: 1.79}
.work-process .medium-heading {margin-bottom: 7.5rem}
.work-process .row + .row {margin-top: 8.75rem}
.work-process .col-img figure img {display: block; width: 100%}
.work-process .col-text {position: relative; margin-bottom: 3.375rem; padding: 6rem 3rem 0 3.75rem; background-color: #f7f7f7}
.work-process .col-text .step-typo {position: absolute; top: -1.875rem; left: -1.5rem; height: 5.125rem; z-index: 5}
.work-process .col-text:not(.order-md-1) .step-typo {left: auto; right: -1.5rem}
.work-process .col-text h3 {font-size: 1.5rem; font-weight: 700; color: #212529; letter-spacing: -0.04em; margin-bottom: 1.5rem}
.work-process .col-text p {font-size: 1.125rem; font-weight: 300; color: #343a40; line-height: 1.79}
.work-process .col-text .button {margin-top: 3rem; font-weight: 700}
.work-process .col-text .button i {margin-left: 1rem; vertical-align: 0}

@media (max-width: 991px) {
    .about-intro .about-cover {margin-bottom: 5rem}
    .about-intro .about-cover h2 {font-size: 3.25rem; margin-top: -4.625rem}
    .about-intro .about-row .col-heading {margin-bottom: 2rem}
    .about-intro .about-row .col-heading h3 {font-size: 2rem}

    .core-value .medium-heading {margin-bottom: 5rem}
    .core-value .col-value figure img {height: 3.25rem}
    .core-value .col-value figure figcaption h3 {font-size: 2rem}
    .core-value .col-value figure figcaption h4 {font-size: 1.25rem}
    .core-value .col-value p {font-size: 1.0625rem}

    .work-process .medium-heading {margin-bottom: 5.625rem}
    .work-process .row + .row {margin-top: 5rem}
    .work-process .col-text {padding: 4.5rem 2.5rem 3rem 3rem}
    .work-process .col-text .step-typo {height: 4rem}
    .work-process .col-text h3 {font-size: 1.3125rem}
    .work-process .col-text p {font-size: 1rem}
}

@media (max-width: 767px) {
    .about-intro .about-cover {margin-bottom: 3.75rem}
    .about-intro .about-cover h2 {font-size: 2.75rem; margin-top: -4rem; padding-left: 3rem}
    .about-intro .about-cover h2 span {padding-left: 3.75rem}
    .about-intro .about-row .col-heading h3 {font-size: 1.75rem}
    .about-intro .about-row .col-desc p {font-size: 1.0625rem}

    .core-value .medium-heading {margin-bottom: 3.75rem}
    .core-value .col-value figure img {height: 2.875rem}
    .core-value .col-value figure figcaption h3 {font-size: 1.75rem}

    .work-process .medium-heading {margin-bottom: 4rem}
    .work-process .row + .row {margin-top: 3rem}
    .work-process .col-text {margin-bottom: 0; padding: 3rem 2rem}
    .work-process .col-text .step-typo {position: static; top: auto; left: auto; height: 2.25rem; margin-bottom: 1.5rem}
    .work-process .col-text p {font-size: 1.0625rem}
    .work-process .col-text .button {margin-top: 2.375rem}
}

@media (max-width: 575px) {
    .about-intro .about-cover {margin-left: -1.5rem; margin-right: -1.5rem}
    .about-intro .about-cover h2 {font-size: 2.375rem; margin-top: -3.625rem}

    .core-value .col-value + .col-value {margin-top: 3rem}

    .work-process .col-text {padding: 2.25rem 2rem}
}