.section {position: relative; padding-top: 10rem; padding-bottom: 10rem}
.section:first-child {padding-top: 0}
.section .large-heading {margin-bottom: 6.25rem}
.section .large-heading h1 {font-size: 3.5rem; font-weight: 700; color: #000; letter-spacing: -0.04em; line-height: 1.42; margin-bottom: 2rem}
.section .large-heading h1 strong {color: #3e78ff}
/* .section .large-heading h1::after {content: '.'; color: #3e78ff} */
.section .large-heading p {font-size: 1.1875rem; color: #212529; line-height: 1.89}
.section .medium-heading h2 {font-size: 3rem; font-weight: 700; color: #000; letter-spacing: -0.04em}

@media (max-width: 991px) {
    .section .large-heading h1 {font-size: 2.625rem}
    .section .medium-heading h2 {font-size: 2.375rem}
}

@media (max-width: 767px) {
    .section {padding-top: 5rem; padding-bottom: 5rem}
    .section .large-heading {margin-bottom: 5rem}
    .section .large-heading h1 {font-size: 2rem; margin-bottom: 1.5rem}
    .section .large-heading p {font-size: 1.125rem}
}